import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {ENERGY_REGEN,SOC, BATTERY_TEMP, DIST_TRAVELLED, ENERGY_CONS, DISCHARGE_CYCLES} from '../reducers/graphTypes'
import {updateGraphDetails} from '../reducers/actionCreators'
import store from '../reducers/store'
import {makeQuery} from '../functions/makeQuery'

export default function Graph() { 
    // eslint-disable-next-line
    const unsubscribe = store.subscribe(()=>{
        // console.log('State was just changed !', store.getState());
        console.log('--------------------------------------');
    })
    // console.log(unsubscribe);
    const handleClick = (string) => {
        console.log(string);
        store.dispatch(updateGraphDetails(string))
        makeQuery(store.getState())
    }

    return (
        <div  className="graph-container">
            <h2>GRAPH</h2>
            <table className="graph-info">
            <thead>
                <tr>
                    <th className="left-top">Meaning</th>
                    <th>Y-Axis</th>
                    <th className="right-top">X-Axis</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>Indicates battery charge remaining over time</td>
                    <td>SOC (State of charge)</td>
                    <td>Time</td>
                </tr>
                <tr>
                    <td>Indicates energy gained while braking over time</td>
                    <td>E Regen</td>
                    <td>Time</td>
                </tr>
                <tr>
                    <td>Indicates temperature of battery over time</td>
                    <td>Battery temp</td>
                    <td>Time</td>
                </tr>
                <tr>
                    <td>Indicates energy consumed over time</td>
                    <td>E cons</td>
                    <td>Time</td>
                </tr>
                <tr>
                    <td>Indicates distance travelled over time</td>
                    <td>Dist travelled</td>
                    <td>Time</td>
                </tr>
                <tr>
                    <td className="left-bottom">Indicates number of charging, discharging events over time</td>
                    <td>Discharge cycles</td>
                    <td className="right-bottom">Time</td>
                </tr>
            </tbody>
            </table>
            <div className="graph">
                <div className="graph-plot section-1">
                    <h3>Graph Plot</h3>
                    <div className="canvas-container">
                        <canvas></canvas>
                    </div>
                </div>
                <div className="graph-options section-2">
                    <h3>Choose a graph</h3>
                    <div className="choose-graph">
                        <button onClick={()=>handleClick(SOC)} className="graph-btn"><FontAwesomeIcon icon="bolt"></FontAwesomeIcon>&nbsp; SOC <span>VS</span> time</button>
                        <button onClick={()=>handleClick(BATTERY_TEMP)} className="graph-btn"><FontAwesomeIcon icon="car-battery"></FontAwesomeIcon>&nbsp; Battery temp <span>VS</span> time</button>
                        <button onClick={()=>handleClick(ENERGY_REGEN)} className="graph-btn"><FontAwesomeIcon icon="atom"></FontAwesomeIcon>&nbsp; Energy <span>VS</span> time</button>
                        <button onClick={()=>handleClick(DIST_TRAVELLED)} className="graph-btn"><FontAwesomeIcon icon="directions"></FontAwesomeIcon>&nbsp; Dist travelled <span>VS</span> time </button>
                        <button onClick={()=>handleClick(ENERGY_CONS)} className="graph-btn"><FontAwesomeIcon icon="battery-half"></FontAwesomeIcon>&nbsp; Energy cons <span>VS</span> time </button>
                        <button onClick={()=>handleClick(DISCHARGE_CYCLES)} className="graph-btn"><FontAwesomeIcon icon="spinner"></FontAwesomeIcon>&nbsp; Discharge cycles <span>VS</span> time</button>
                    </div>    
                </div>
            </div>
        </div>
    )
}
