import React, {useState} from 'react';
import store from '../reducers/store'
import {updateXDetails} from '../reducers/actionCreators'
import {makeQuery} from '../functions/makeQuery'

export default function Form() {
    // eslint-disable-next-line
    const unsubscribe = store.subscribe(()=>{
        // console.log('State was just changed !', store.getState());
        console.log('--------------------------------------');
    })
    const handleSubmit = (e) => {
        e.preventDefault();
        console.log('Form was just submitted');
        // console.log(vehicleId, startYear, endYear, startMonth, endMonth, startDay, endDay);
        const date = createFormat(startYear, endYear, startMonth, endMonth, startDay, endDay,'/');
        const time = createFormat(startHour, endHour,startMinute,endMinute,startSecond,endSecond,':');

        const xDetails = {
            vehicleId:[startVehicleId,endVehicleId],
            date,
            time
        }

        // console.log(xDetails)
        store.dispatch(updateXDetails(xDetails))
        makeQuery(store.getState());
    }

    const createFormat = (sv1,ev1,sv2,ev2,sv3,ev3,delim)=>{
        const startPart = `${sv3}${delim}${sv2}${delim}${sv1}`;
        const endPart = `${ev3}${delim}${ev2}${delim}${ev1}`;
        return [startPart, endPart];
    }

    const [startVehicleId, setstartVehicleId] = useState('');
    const [endVehicleId, setendVehicleId] = useState('');
    const [startYear, setstartYear] = useState('');
    const [endYear, setendYear] = useState('');
    const [startMonth, setstartMonth] = useState('');
    const [endMonth, setendMonth] = useState('');
    const [startDay, setstartDay] = useState('');
    const [endDay, setendDay] = useState('');
    
    const [startHour, setstartHour] = useState('');
    const [endHour, setendHour] = useState('');
    const [startMinute, setstartMinute] = useState('');
    const [endMinute, setendMinute] = useState('');
    const [startSecond, setstartSecond] = useState('');
    const [endSecond, setendSecond] = useState('');
    

    return (
        <div className="input-form">
            <h2>Input Details</h2>
            <form  onSubmit={(e)=>handleSubmit(e)} className="form">
                <div className="date-duration">
                    <label htmlFor="date-duration" className="form-label">Select Day, Date, Time</label>
                    <p className="input-info">The vehicle data will be analyzed, averaged and graphed as per the specified day, month or year time interval. <br/> <br/> Eg : Between 1/2/2020 and 3/2/2020 </p>
                    <table>
                        <thead>
                            <tr>
                                <th>YEAR</th>
                                <th>MONTH</th>
                                <th>DAY</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>
                                    <p>Enter the start and end year in YYYY format</p>
                                    <input onChange={(e)=>{setstartYear(e.target.value)}} value={startYear}  type="number" min='2001' max='2020' name="start-year" id="start-year" className="form-input" placeholder="Eg : 2019"/>
                                    <input onChange={(e)=>{setendYear(e.target.value)}} value={endYear}  type="number" min='2001' max='2020' name="end-year" id="end-year" className="form-input" placeholder="Eg : 2020"/>
                                </td>
                                <td>
                                    <p>Enter the start and end month in MM format</p>
                                    <input onChange={(e)=>{setstartMonth(e.target.value)}} value={startMonth}  type="number" min='1' max='12' name="start-month" id="start-month" className="form-input" placeholder="Eg : 02"/>
                                    <input onChange={(e)=>{setendMonth(e.target.value)}} value={endMonth}  type="number" min='1' max='12' name="end-month" id="end-month" className="form-input" placeholder="Eg : 03"/>
                                </td>
                                <td>
                                    <p>Enter the start and end day in DD format</p>
                                    <input onChange={(e)=>{setstartDay(e.target.value)}} value={startDay}  type="number" min='1' max='31' name="start-day" id="start-day" className="form-input" placeholder="Eg : 01"/>
                                    <input onChange={(e)=>{setendDay(e.target.value)}} value={endDay}  type="number" min='1' max='31' name="end-day" id="end-day" className="form-input" placeholder="Eg : 18"/>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <div className="vehicle-id">
                    <label htmlFor="vehicle-id" className="form-label">Select Vehicle(s)</label>
                    <p className="input-info">Enter start and end vehicle IDs of the range of vehicles that you wish to evaluate </p>
                    <div className="vehicle-inputs">
                        <label htmlFor="start-vehicle-id" className="form-label">Set Start Vehicle ID</label>
                        <label htmlFor="end-vehicle-id" className="form-label">Set End Vehicle ID</label>
                        <input onChange={(e)=>{setstartVehicleId(e.target.value)}} value={startVehicleId} required type="text" name="start-vehicle-id" id="start-vehicle-id" className="form-input" placeholder="Eg : EV20201"/>
                        <input onChange={(e)=>{setendVehicleId(e.target.value)}} value={endVehicleId} required type="text" name="end-vehicle-id" id="end-vehicle-id" className="form-input" placeholder="Eg : EV20203"/>
                    </div>
                    <p className="input-info" > <br/> Eg : For range = EV20201, EV20202, EV20203 <br/>
                    Start Vehicle ID = EV20201 <br/> End Vehicle ID = EV20203 </p>
                </div>                
                <div className="time-duration">
                    <p className="form-label">Select Time Duration</p>
                    <p className="input-info">Given a day, the data can be visualized differently as per different time intervals. For date durations bigger than day, entire day's data will be returned <br/> <br/> Eg : Between 14:30:30 and 15:30:00 on 13/12/2020 </p>
                    <table>
                        <thead>
                            <tr>
                                <th>HOUR</th>
                                <th>MINUTE</th>
                                <th>SECOND</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>
                                    <p>Enter the start and end hour in HH format</p>
                                    <input onChange={(e)=>{setstartHour(e.target.value)}} value={startHour}  type="number" min='01' max='24' name="start-hr" id="start-hr" className="form-input" placeholder="Eg : 02"/>
                                    <input onChange={(e)=>{setendHour(e.target.value)}} value={endHour}  type="number" min='01' max='24' name="end-hr" id="end-hr" className="form-input" placeholder="Eg : 16"/>
                                </td>
                                <td>
                                    <p>Enter the start and end minute in MM format</p>
                                    <input onChange={(e)=>{setstartMinute(e.target.value)}} value={startMinute}  type="number" min='1' max='60' name="start-min" id="start-min" className="form-input" placeholder="Eg : 02"/>
                                    <input onChange={(e)=>{setendMinute(e.target.value)}} value={endMinute}  type="number" min='1' max='60' name="end-min" id="end-min" className="form-input" placeholder="Eg : 03"/>
                                </td>
                                <td>
                                    <p>Enter the start and end second in SS format</p>
                                    <input onChange={(e)=>{setstartSecond(e.target.value)}} value={startSecond}  type="number" min='1' max='60' name="start-sec" id="start-sec" className="form-input" placeholder="Eg : 01"/>
                                    <input onChange={(e)=>{setendSecond(e.target.value)}} value={endSecond}  type="number" min='1' max='60' name="end-sec" id="end-sec" className="form-input" placeholder="Eg : 18"/>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <div className="submit-n-err">
                    <div className="error-box">This is where errors will be shown</div>        
                    <button type="submit" className="submit-btn">Submit</button>
                </div>
            </form>
        </div>
    )
}
